import React from "react";
import './App.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlay } from "@fortawesome/free-solid-svg-icons";
import { faPause } from "@fortawesome/free-solid-svg-icons";
import { faArrowDown } from "@fortawesome/free-solid-svg-icons";
import { faArrowUp } from "@fortawesome/free-solid-svg-icons";
import * as math from "mathjs";
import beep from "./beep.wav";


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      type: "Session",
      breakTimer: 5,
      sessionTimer: 25,
      timerMinutes: 25,
      timerSeconds: "00",
      play: true,
      timers: []
    }
    this.handleSessionIncrement = this.handleSessionIncrement.bind(this);
    this.handleSessionDecrement = this.handleSessionDecrement.bind(this);
    this.handleBreakIncrement = this.handleBreakIncrement.bind(this);
    this.handleBreakDecrement = this.handleBreakDecrement.bind(this);
    this.handlePlayPause = this.handlePlayPause.bind(this);
    this.handleReset = this.handleReset.bind(this);
    this.countdown = this.countdown.bind(this);
  }

  handleSessionIncrement(event) {
    if(this.state.sessionTimer >= 1 && this.state.sessionTimer < 60) {
      if(this.state.type === "Session") {
        this.setState({
          ...this.state,
          sessionTimer: this.state.sessionTimer + 1,
          timerMinutes: this.state.timerMinutes < 9 ? "0" + parseInt(this.state.sessionTimer + 1) : parseInt(this.state.sessionTimer + 1)
        });
      }
      else {
        this.setState({
          ...this.state,
          sessionTimer: this.state.sessionTimer + 1,
        });
      }
    }
  }

  handleSessionDecrement(event) {
    if(this.state.sessionTimer > 1 && this.state.sessionTimer < 60) {
      if(this.state.type === "Session") {
        this.setState({
          ...this.state,
          sessionTimer: this.state.sessionTimer - 1,
          timerMinutes: this.state.timerMinutes < 11 ? "0" + parseInt(this.state.sessionTimer - 1) : parseInt(this.state.sessionTimer - 1)
        });
      }
      else {
        this.setState({
          ...this.state,
          sessionTimer: this.state.sessionTimer - 1
        });
      }
    }
  }

  handleBreakIncrement(event) {
    if(this.state.breakTimer >= 1 && this.state.breakTimer < 60) {
      if(this.state.type === "Break") {
        this.setState({
          ...this.state,
          breakTimer: this.state.breakTimer + 1,
          timerMinutes: this.state.timerMinutes < 9 ? "0" + parseInt(this.state.breakTimer + 1) : parseInt(this.state.breakTimer + 1)
        });
      }
      else {
        this.setState({
          ...this.state,
          breakTimer: this.state.breakTimer + 1
        });
      }
    }
  }

  handleBreakDecrement(event) {
    if(this.state.breakTimer > 1 && this.state.breakTimer < 60) {
      if(this.state.type === "Break") {
        this.setState({
          ...this.state,
          breakTimer: this.state.breakTimer - 1,
          timerMinutes: this.state.timerMinutes < 11 ? "0" + parseInt(this.state.breakTimer - 1) : parseInt(this.state.breakTimer - 1)
        });
      }
      else {
        this.setState({
          ...this.state,
          breakTimer: this.state.breakTimer - 1
        });
      }
    }
  }

  countdown() {
    let time = parseInt(this.state.timerMinutes) * 60 + parseInt(this.state.timerSeconds);
    if(this.state.timerMinutes === "00" && this.state.timerSeconds === "00" && !this.state.play) {
      this.switchTimer();
    }
    else {
      this.setState({
        ...this.state,
        timerSeconds: this.state.timerSeconds < 11 && this.state.timerSeconds > 0 ? "0" + math.round((time-1) % 60) : math.round((time-1) % 60),
        timerMinutes: this.state.timerMinutes < 10 ? "0" + parseInt((this.state.timerSeconds === "00" || this.state.timerSeconds === 60) ? this.state.timerMinutes - 1 : this.state.timerMinutes) : (this.state.timerSeconds === "00" || this.state.timerSeconds === 60) ? this.state.timerMinutes - 1 : this.state.timerMinutes,
        timers: this.state.timers.concat(setTimeout(this.countdown, 1000))
      });
    }
    console.log(this.state.timerMinutes + ":" + this.state.timerSeconds);

  }

  switchTimer() {
    document.getElementById('beep').play();

    if(this.state.type === "Session") {
      this.cancelTimer();
      this.setState({
        ...this.state,
        type: "Break",
        timerMinutes: this.state.breakTimer,
        timerSeconds: "00",
        timers: this.state.timers.concat(setTimeout(this.countdown, 1000))
      });
    }
    else if(this.state.type === "Break") {
      this.cancelTimer();
      this.setState({
        ...this.state,
        type: "Session",
        timerMinutes: this.state.sessionTimer,
        timerSeconds: "00",
        timers: this.state.timers.concat(setTimeout(this.countdown, 1000))
      })
    }
  }

  cancelTimer() {
    console.log(this.state.timers.length);
    for(let i = 0; i < this.state.timers.length; i++) {
      clearTimeout(this.state.timers[i]);
      console.log("timer cleared");
    }
  }

  handlePlayPause(event) {
    if(this.state.play) {
      this.countdown();
      this.setState({
        ...this.state,
        play: false,
        timerMinutes: this.state.timerMinutes < 10 && this.state.timerMinutes[0] !== "0" ? "0" + (this.state.timerSeconds === "00" ? this.state.timerMinutes : this.state.timerMinutes) : (this.state.timerSeconds === "00" ? this.state.timerMinutes : this.state.timerMinutes)
      });
    }
    else if(!this.state.play){
      this.cancelTimer();
      this.setState({
        ...this.state,
        play: true,
        timers: []
      });
    }
  }

  handleReset(event) {
    this.cancelTimer();
    document.getElementById('beep').pause();
    document.getElementById('beep').currentTime = 0;

    this.setState({
      type: "Session",
      breakTimer: 5,
      sessionTimer: 25,
      timerMinutes: 25,
      timerSeconds: "00",
      play: true,
      timers: []
    });
  }

  render() {
    return (
      <div className="App">
        <h1 id="title">25 + 5 Clock</h1>
        <div className="container">
          <div className="row justify-content-between">
            <div className="col-sm-1 col-xs-1"></div>
            <div className="section col-sm-4 col-xs-4">
              <p id="break-label">Break Length</p>
              <p id="break-length">{this.state.breakTimer}</p>
              <button id="break-decrement" className="btn" onClick={this.handleBreakDecrement}><FontAwesomeIcon icon={faArrowDown} /></button>
              <button id="break-increment" className="btn" onClick={this.handleBreakIncrement}><FontAwesomeIcon icon={faArrowUp} /></button>
            </div>
            <div className="section col-sm-4 col-xs-4">
              <p id="session-label">Session Length</p>
              <p id="session-length">{this.state.sessionTimer}</p>
              <button id="session-decrement" className="btn" onClick={this.handleSessionDecrement}><FontAwesomeIcon icon={faArrowDown} /></button>
              <button id="session-increment" className="btn" onClick={this.handleSessionIncrement}><FontAwesomeIcon icon={faArrowUp} /></button>
            </div>
          </div>

          <div id="timer-section">
            <p id="timer-label" className="timer-display">{this.state.type}</p>
            <p id="time-left" className="timer-display">{this.state.timerMinutes + ":" + this.state.timerSeconds}</p>
            <button id="start_stop" className="btn" onClick={this.handlePlayPause}><FontAwesomeIcon icon={faPlay} /><FontAwesomeIcon icon={faPause} /></button>
            <button id="reset" className="btn" onClick={this.handleReset}>Reset</button>
            <audio id="beep">
              <source src={beep} type="audio/wav" />
            </audio>
          </div>
        </div>
        <div>
          <p id="name">Created by: Andrew Hatch</p>
        </div>
      </div>
    )};
}

export default App;
